import React, { useState } from 'react';
import { Button, Col, Container, Form, FormControl, InputGroup, Row } from 'react-bootstrap';
import { useHistory, useLocation } from 'react-router';
import useAuth from '../useAuth/useAuth';
import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword  } from "firebase/auth";

const Login = () => {
  const {signInWithGoogle, user, githubHendler} = useAuth();
  const auth = getAuth();
  const location = useLocation();
  const history = useHistory();
  const [email, setEmail]= useState("");
  const [password, setPassword]= useState ("");
  const [isLogin, setIsLogin]= useState(false);
  const [error, setError]= useState ("");
const redirect_url = location.state?.from ;
  const hendleGoogleLogin=()=>{
    signInWithGoogle()
    .then(result => {
     history.push(redirect_url)
  })

  }
    const hendleGitLogin=()=>{
      githubHendler()
      .then(result => {
        history.push(redirect_url)
     })
    }
 
    // create user 
    const emailId =(e)=>{
      setEmail(e.target.value);
    };

    const pasHendle =(e)=>{
      setPassword(e.target.value);
    };
     
    const toggleLogin =(e)=>{
      setIsLogin( e.target.checked);
    }

const hendleRegister=()=>{

console.log(email);
console.log(password);
  if(password.length <6 ){
    setError('পাসওয়ার্ড 6 ডিজটি এর বেশি লাগেবে ');
    return;
  }





const createUser=(auth, email, password )=>{
   createUserWithEmailAndPassword(auth, email, password)

  .then(result=>{
const user=result.user;
console.log(user);
  })
  .catch(error => {
    setError(error.massage);
    
  })
}

 const procesLogin=(auth, email, password)=>{
  signInWithEmailAndPassword (auth , email, password)
  .then(result => {
    const user = result.user;
    console.log(user);
   
  })
  .catch(error =>{
    setError('error.massage')
  })
 } 
 isLogin? procesLogin(auth, email, password) : createUser(auth, email, password);

}

    return (
        <div className="container " >
          <div className="text-center">
          <h1>Please {isLogin ? 'login': 'Register' }</h1>
         <Container>
          <Row>
          <Col lg={6} md={6} >
                        <Form>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                <Form.Label>Email</Form.Label>
                                <Form.Control onBlur={emailId} type="Email" placeholder="Your Email" required />
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                                <Form.Label>Password</Form.Label>
                                <Form.Control onBlur={pasHendle} type="password" placeholder="Your Password" />
                               
                            </Form.Group>
                            <InputGroup className="mb-3">
    <InputGroup.Checkbox onChange={toggleLogin} aria-label="Checkbox for following text input" />
<p>Are You Regesred? </p>
  </InputGroup>
                            <Button varient="primary" onClick={hendleRegister} > {isLogin?"Login" : "Create" } </Button>
                        </Form>
                    </Col> 
                    <p>{error}</p>
          </Row>
         </Container>

          <hr />
            <Button variant="info" className='m-2' onClick={hendleGoogleLogin} >Sign in With Google </Button>
            <Button variant="info" className='m-2'  onClick={hendleGitLogin} >Sign in With github </Button>
          </div>
        </div>
    );
};

export default Login;