import React from 'react';
import mapImage from '../image/map.png';
import { Button, Col, Container, Form, Row } from 'react-bootstrap';

const GetInTouch = () => {
    return (
        <div>
            <Container className="p-0 pt-5 pb-5" >
               <Row>
               <p>Come and visit</p>
                <h1>Get in touch </h1>
                <p>Come and visit our quarters or simply send us an email anytime you want. We are open to all suggestions from our audience.
                </p>

               </Row>

                <Row >
                    <Col lg={6} md={6} >
                        <Form>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                <Form.Label>Email address</Form.Label>
                                <Form.Control type="email" placeholder="name@example.com" />
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                                <Form.Label>Massage</Form.Label>
                                <Form.Control as="textarea" rows={3} />
                            </Form.Group>
                            <Button varient="primary" > Submit </Button>
                        </Form>
                    </Col>
                    <Col lg={6} md={6}>
                        <img src={mapImage} alt="" className="img-fluid ps-5 " />
                    </Col>

                </Row>
            </Container>
        </div>
    );
};

export default GetInTouch;