import React from 'react';
import infoImg from '../image/bookingInfo.jpg';
import { Col, Container, Form, Row, Button } from 'react-bootstrap';

const About = () => {
    return (
        <div>
            <Container>
                <div className="about">
                    <span>About us</span>
                    <h1>Appointments</h1>
                    <p>Appropriately empower dynamic leadership skills after business portals. Globally myocardinate interactive supply chains with distinctive quality vectors</p>
                </div>
                <div className="bocking-section">
                    <span>Get the right medical care</span>
                    <h1>Book with your doctor</h1>
                    <p>Some up and coming trends are healthcare consolidation for independent healthcare centers that see a cut in unforeseen payouts.</p>
                </div>
                <div className="booking-info">
                    <Row>
                        <Col lg={4} md={4}>
                            <div className="info-img">
                                <img src={infoImg}
                                    className="w-100" alt="" />
                            </div>
                             <div className="mobile mt-5 pt-2">
                                 <h4 className="d-flex align-items-center justify-content-around"><span><i class="fas fa-2x fa-phone-square-alt"></i></span>01xxxxxx 8565</h4>
                                 <h4 className="d-flex align-items-center justify-content-around" ><span><i class="fas fa-2x fa-envelope"></i></span>admin@gmail.com</h4>
                             </div> 

                        </Col>
                        <Col lg={8} md={8}>
                            <Form>
                                <Row className="mb-3">
                                    <Form.Group as={Col} controlId="formGridEmail">
                                        <Form.Label>Name</Form.Label>
                                        <Form.Control type="text" placeholder="Your Full Name" />
                                    </Form.Group>
                                    <Form.Group as={Col} controlId="formGridEmail">
                                        <Form.Label>Email</Form.Label>
                                        <Form.Control type="email" placeholder="Enter email" />
                                    </Form.Group>
                                </Row>

                                <Form.Group className="mb-3" controlId="formGridAddress1">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control placeholder="1234 Main St" />
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="formGridAddress2">
                                    <Form.Label>Address 2</Form.Label>
                                    <Form.Control placeholder="Apartment, studio, or floor" />
                                </Form.Group>

                                <Row className="mb-3">
                                    <Form.Group as={Col} controlId="formGridCity">
                                        <Form.Label>City</Form.Label>
                                        <Form.Control />
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="formGridState">
                                        <Form.Label>State</Form.Label>
                                        <Form.Select defaultValue="Choose...">
                                            <option>Choose...</option>
                                            <option>...</option>
                                        </Form.Select>
                                    </Form.Group>

                                    <Form.Group as={Col} controlId="formGridZip">
                                        <Form.Label>Zip</Form.Label>
                                        <Form.Control />
                                    </Form.Group>
                                </Row>

                                <Form.Group className="mb-3" id="formGridCheckbox">
                                    <Form.Check type="checkbox" label="Check me out" />
                                </Form.Group>

                                <Button variant="primary" type="submit">
                                    Submit
                                </Button>
                            </Form>
                        </Col>
                    </Row>
                </div>
            </Container>
        </div>
    );
};

export default About;